import React from 'react'

const Statement = () => (
  <div className="statement">
    <h1 className="statement__title">Mike Lee</h1>
    <h3 className="statement__subtitle">
      Web developer, designer, and cat lover 🐱
    </h3>
  </div>
)

export default Statement



